import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';

@Injectable()
export class UserService {

  constructor(
    private http: HttpClient,
  ) {}

  userlist(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/user/salesuser/`,data);
  }

  warehouseUserList(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/user/warehouseuser/`,data);
  }

  getUser(id) : Observable<any>{
    return this.http.get<any>(`${environment.base_url}/user/${id}/`);
  }

  register(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/register/`, data);
  }

  editprofile(data,id): Observable<any> {
    return this.http.put<any>(`${environment.base_url}/editprofile/${id}`, data);
  }

  deleteuser(id): Observable<any> {
    return this.http.delete<any>(`${environment.base_url}/deleteuser/${id}/`);
  }
}
