import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';

@Injectable()
export class PaymentReceivedService {

  constructor(private http: HttpClient) { }

  paymentreceived(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/paymentreceived/`, data);
  }

  getCustomer(id) : Observable<any>{
    return this.http.get<any>(`${environment.base_url}/customer/${id}/`);
  }

  displayorder(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/orderdisplay/displayorder/`,data);
  }

  updatepaymentdetail(data,id): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/updatepaymentdetail/${id}/`,data);
  }

  clear_payment(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/clear_payment/`, data);
  }

  update_payment_history(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/update_payment_history/`,data);
  }
}
